import React from 'react';
import { Illustration } from 'react-zdog';
import { RotatingAnchor, ShapeExtrusion } from '../src';

const Index = () => (
  <div style={{width: '800px', height: '800px'}}>
    <Illustration>
        <RotatingAnchor>
            <ShapeExtrusion depth={50} spacing={(i, v) => ({ x: 0, y: -Math.sin(v * Math.PI) * 10, z: i })}/>
        </RotatingAnchor>
    </Illustration>
  </div>
);

export default Index;